body{
    direction: ltr;
    font-family: 'Open Sans', sans-serif;
    &.overflow{
        overflow: hidden;
    }
    background-color: #242424;
}
/*navbar*/
.close {
    float: right;
    font-size: 20px;
    font-weight: 700;
    line-height: unset;
    text-shadow: unset;
    opacity: .7;
}


.search-overlay{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-13%,-93%) scale(.1);
    background-color: rgba(41, 41, 41, 0.95);
    width: 100%;
    height: 100%;
    z-index: 9000;
    opacity: 0;
    visibility: hidden;
    transition: .5s;
    &.active{
        transform: translate(-50%,-50%) scale(1);
        opacity: 1;
        visibility: visible;
    
    }
    .close{
        position: absolute;
        top: 11%;
        left: 13%;
        font-size: 30px;
        color: rgb(88, 88, 88);
        transition: .5s;
        &:hover{
            color: #9ec050
        }
    }
    .form{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: max-content;
        position: relative;
        input{
            background-color: transparent;
            border: none;
            border-bottom:2px solid  rgb(55, 55, 55);
            padding: 10px 20px;
            width: 550px;
            outline: none;
            color: #999;
            transition: .5s;
            &:focus{
                border-bottom:2px solid  #9ec050;
            }
            @media (max-width:425px) {
                width: 350px;
            }
            @media (max-width:375px ) {
                width: 250px;
            }

        }
        button{
            background-color: transparent;
            border: none;
            color:  rgb(139, 138, 138);
            cursor: pointer;
            position: absolute;
            font-size: 17px;
            top: 10px;
            right: 8px;
            transition: .5s;
            outline: none;
            &:hover{
                color: #9ec050;
            }    

        }
    }
}

.side-nav{
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: rgba(41, 41, 41, 0.95);
    width: 100%;
    height: 100%;
    z-index: 9000;
    transition: .5s;
    opacity:0;
    visibility: hidden;
    transform: translate(-6%, -92%) scale(0.1);

    &.nav-active{
        transform: translate(-50%,-50%) scale(1);
        opacity: 1;
        visibility: visible;
    
    }
    .top{
        position: absolute;
        left: 12%;
        top: 10%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .close{
            font-size: 30px;
            color: rgb(88, 88, 88);
            transition: .5s;
            &:hover{
                color: #9ec050
            }
        }    
        .lang{
            margin-top: 20px;
            a{
               font-size: 16px;
               color: #fff;
               font-family: "Arial";
               font-weight: bold;
               transition: .5s;
           }
           border: 1px solid rgb(52, 52, 52);
           width: 40px;
           height: 40px;
           display: flex;
           justify-content: center;
           align-items: center;
           font-size: 12px;
           color: rgb(255, 255, 255);
           cursor: pointer;
           &:hover{
               a{

                   color: #9ec050;
               }
            }
        }
    }
    .nav-link{
        position: absolute;
        top: 33%;
        left: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        margin-top: auto;
        height: 100px;
        @media (max-width:320px) {
            top: 37%;
        }

        &>li{
            margin-bottom: 10px;
            & > a{
                font-size: 25px;
                @media (max-width:425px) {
                    font-size: 23px;
                }
                @media (max-width:320px) {
                    font-size: 19px;
                }

                color: rgb(255, 255, 255);
                text-transform: uppercase;
                text-decoration: none;
                transition: .5s;
                backface-visibility: hidden;
                &:hover{
                    color: #9ec050;
                }
            }
        }
        .drop{
            display: block;
            align-items: center;
            ul{
                padding-inline-start: 0;
                backface-visibility: hidden;
                li{
                    padding: 7px 0; 
                    @media (max-width:320px) {
                        padding: 0 0; 
                    }
            
                    &:not(:last-child){
                        border-bottom: 1px solid rgb(55, 55, 55);
                    }
                    a{
                        font-size: 13px;
                        color: rgb(119, 118, 118);
                        text-decoration: none;
                        &:hover{
                            color: #9ec050;
                        }    
                    }
    
                }
            }

        }
    }
}

.big-nav{
    .main-nav{
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        padding: 24px 0 10px;

        .logo-box{
            position: absolute;
            left: -1%;
            top: 0;
            z-index: 1111;

            img{
                width: auto;
                @media (max-width:1024px) {
                    width: 85%;
                }
                @media (max-width:991px) {
                    width: 80%;
                }
                @media (max-width:480px) {
                    width: 70%;

                }
                @media (max-width:375px) {
                    width: 60%;

                }
                
    }
        }
        .nav-link{
            display: flex;
            position: relative;
            margin-left: auto;
            margin-right: 30px;
            @media (max-width:991px) {
                display: none;
            }
        
            & > li{
                margin-right: 30px;
                @media (max-width:1024px) {
                    margin-right: 20px;
                }

                & > a{
                    font-size: 14px;
                    color: rgb(255, 255, 255);
                    text-transform: uppercase;
                    text-decoration: none;
                    transition: .5s;
                    &:hover{
                        color: #9ec050;
                    }
                }
            }
            .drop{
                .sup-link{
                    z-index: 1111;
                    position: absolute;
                    top: 42px;
                    left: 160px;
                    background-color: rgba(41, 41, 41, 0.902);
                    width: 279px;
                    height: 175px;
                    padding: 10px 17px;
                    margin: 0;
                    transform: scale(.5) translateY(-100px);
                    transition: .5s;
                    opacity: 0;
                    visibility: hidden;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    li{
                        padding: 7px 0; 
                        &:not(:last-child){
                            border-bottom: 1px solid rgb(55, 55, 55);
                        }
                        a{
                            font-size: 13px;
                            color: rgb(119, 118, 118);
                            transition: .5s;
                            text-decoration: none;
                            &:hover{
                                color: #9ec050;
                            }    
                        }
                    }  
                }
                &:hover{
                    .sup-link{
                        transform: scale(1) translateY(0);
                        opacity:1;
                        visibility: visible;
                    }
                }
            }
        }
        .nav-icon{
        
            display: flex;
            justify-content: center;
            align-items: center;
            @media (max-width:991px) {
                display: none;
            }

            li{
                margin-right: 23px;
                @media (max-width:1024px) {
                    margin-right: 15px;
                }
                a{
                    font-size: 16px;
                    color: rgb(88, 88, 88);
                    transition: .5s;
                    &:hover{
                        color: #fff;
                    }
                }
                &.search-icon{
                    margin-left: 18px;
                    margin-right: 8px;
                }
                &.search-icon,
                &.lang a{
                    font-size: 16px;
                    color: #fff;
                    font-family: "Arial";
                    font-weight: bold;
                    transition: .5s;
                }
                &.search-icon,
                &.lang ,
                &.sm-nav{
                
                    border: 1px solid rgb(52, 52, 52);
                    width: 40px;
                    height: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 12px;
                    color: rgb(255, 255, 255);
                    cursor: pointer;
                    &:hover{
                        a{

                            color: #9ec050;
                        }
                        color: #9ec050;
                    }    

                }
                &.lang{
                    margin-right: 45px;
                    @media (max-width:991px) {
                        margin-right: 8px;
                        display: none;
                        
                    }
        
                }
            }
        }
        .sm-icon{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-left: auto;
            display: none;
            @media (max-width:991px) {
                display: flex;
            }

            li{
                &.search-icon{
                    margin-right: 15px;
                }
                &.search-icon,
                &.sm-nav{
                    transition: .5s;
                    
                    border: 1px solid rgb(52, 52, 52);
                    width: 40px;
                    height: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 16px;
                    color: rgb(255, 255, 255);
                    cursor: pointer;
                    &:hover{
                        a{

                            color: #9ec050;
                        }
                        color: #9ec050;
                    }    

                }
            }

        }
    }
}
.main-header{
    overflow: hidden;
    .con{
        width: 93%;
        margin: auto;
        position: relative;
        @media (max-width:991px) {
            width: 100%;
        }
    
        .caption{
            position: absolute;
            top: 50%;
            left: 25%;
            transform: translate(-50% , -50%);
            @media (max-width:1024px) {
                left: 30%;
            }
            @media (max-width:991px) {
                left: 29%;
            }
            @media (max-width:768px) {
                left: 35%;

            }
            @media (max-width:480px) {
                text-align: center;
                left: 50%;
                top: 55%;
                width: 80%;
            }

            h2{
                font-size: 48px;
                color: rgb(255, 255, 255);
                font-weight: bold;
                @media (max-width:768px) {
                    font-size: 38px;

                }
                @media (max-width:480px) {
                    font-size: 25px;

                }
                @media (max-width:375px) {
                    font-size: 23px;

                }
                @media (max-width:320px) {
                    font-size: 19px;

                }
            }
            p{
                font-size: 20px;
                color: rgb(255, 255, 255); 
                @media (max-width:768px) {
                    font-size: 15px;

                }
                @media (max-width:480px) {
                    font-size: 14px;

                }
                @media (max-width:320px) {
                    font-size: 12px;

                }
                 
            }
            span{
                display: block;
                @media(max-width:480px){
                    display: unset;
                }
    
            }
        }
        .owl-dots{
            position: absolute;
            top: 65%;
            left: 11%;
            transform: translateX(-50%);
            @media (max-width:1024px) {
                top: 66%;
            }
            @media (max-width:768px) {
                left: 18.5%;

            }
            @media(max-width:480px){
                display: none;
            }
        .owl-dot{
                background-color: rgba(255, 255, 255, 0.302);
                width: 32px;
                height: 2px;
                display: inline-block;
                outline: none;
                transition: .5s;
                &:not(:first-child){
                    margin-right: 8px;
                    @media (max-width:375px) {
                        margin-right: 5px;
    
                    }
                }
                &.active{
                    background-color:#9ec050;
                }
            }
        }
        .owl-nav{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            width: 100%;
            margin-top: 0;
            height: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 30px;
            @media (max-width:991px) {
                display: none;
            }
            .owl-prev,
            .owl-next{
                outline: none;
                width: 45px;
                height: 45px;
                margin: 0;
                background-color: rgb(46, 46, 46);
                transition: .5s;
                &:hover{
                    background-color: #9ec050;
                }
                span{
                    color: #FFF;
                    font-size: 25px;
                    display: block;
                }
            }
        }
        .owl-next{
            position: absolute;
            left:-54px;
        
        }
        .owl-prev{
            position: absolute;
            right: -54px;
        
        }
    
    }
}
//desing
.desing{
    padding: 100px 0 140px;
    @media (max-width:1024px) {
        padding: 60px 0 140px;
    }
    overflow: hidden;
    .desing-contint{
        @media (max-width:991px) {
            margin-bottom: 35px;
        }
        img{
            @media (max-width:1024px) {
                width: 70%;
            }
            @media (max-width:768px) {
                width: 50%;

            }
            @media (max-width:480px) {
                width: 70%;

            }
        }
        p{
            font-size: 15px;
            color: rgb(144, 144, 144);
            line-height: 1.6;
            padding: 37px  78px 0;
            @media (max-width:1024px) {
                font-size: 13px;
            }
            @media (max-width:480px) {
                padding: 37px  60px 0;

            }
            @media (max-width:375px) {
                padding: 37px  45px 0;

            }
            @media (max-width:320px) {
                padding: 37px 10px 0;

            }

        }
        a{
            font-size: 13px;
            color: rgb(79, 79, 79);
            font-style: italic;
            padding-left:78px ;
            text-decoration: none;
            display: block;
            transition: .5s;
            margin-top: 26px;
            @media (max-width:480px) {
                padding-left:60px ;
                margin-top: 16px;


            }
            @media (max-width:375px) {
                padding-left:45px ;
                margin-top: 16px;


            }
            @media (max-width:375px) {
                padding-left:45px ;
                margin-top: 16px;


            }
            @media (max-width:320px) {
                padding-left:10px ;

            }
            &:hover{
                transform: translateX(5px);
                color: rgb(144, 144, 144);
            }
        }
    }
    .img-box{
        position: relative;
        width: max-content;
        margin: auto;
        @media (max-width:480px) {
            width: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            
        }
        img{
            margin: auto !important;
            width: 80%;
            display: block;
            @media (max-width:1024px) {
                margin: 0;
                width: 70%;

            }
            @media (max-width:991px) {
                width: 100%;
            }
            @media (max-width:768px) {
                width: 70%;

            }
            @media (max-width:480px) {
                width: 90%;
                margin: auto !important;

            }
                
            display: block;
            @media (min-width:1920px) {
                width: auto;
            }
        }
        &::before{
            content: '';
            position: absolute;
            bottom: -15%;
            right: -12%;
            border: 20px solid  rgb(43, 43, 43);
            width: 417px;
            height: 356px;
            z-index: -1;
            @media (max-width:768px) {
                right:5%;

            }
            @media (max-width:480px) {
                width: 60%;
                height: 100%;
                right:0;

            }
        }
    }

}
// delivering
.delivering{
    padding: 168px 0;
    @media (max-width:991px) {
        padding: 50px 0;
    }


    background-image: url('../images/01.jpg');
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    h2{
        font-size: 60px;
        color: rgb(255, 255, 255);
        text-align: center;
        font-weight: 900;
        margin-bottom: 42px;
        span{
            font-weight: 200;
            display: block;
        }
        @media (max-width:480px) {
            font-size: 35px;
        }

    }
    .icon-body{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: rgba(36, 36, 36,0.702);
        width: 100%;
        height: 300px;
        text-align: center;
        text-decoration: none;
        transition: .5s;
        &:hover{
            background-color:rgb(158, 192, 80);
        }
        img{
            margin-bottom: 35px;
        }
        p{
            font-size: 16px;
            color: rgb(255, 255, 255);
            line-height: 1.25;
            text-transform: uppercase;
        }
    }
    .owl-item{
        display: flex;
        justify-content: center;
        align-items: center;
        .icon-body,
        .col-md-3{
            flex: unset !important;
            max-width: unset !important;
        }
        .item{
            margin: auto;
        }
    }
    .owl-dot{
        display: inline-block;
        outline: none;
        margin-top: 10px;
    }
    .owl-theme .owl-dots .owl-dot.active span{
        background: #9ec050;
    }
    .owl-theme .owl-dots .owl-dot span{
        background: #242424;

    }
    .owl-carousel .owl-stage-outer{
        margin-right: -16px;

    }
}
.works{
    padding: 150px 0;
    @media (max-width:991px) {
        padding: 70px 0;
    }

    h2{
        font-size: 48px;
        color: rgb(255, 255, 255);
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 60px;
    }      
    .img-container{
        display: flex;
        justify-content: center;
        margin: auto;
        flex-wrap: wrap;
        img{
            width: 100%;
            height: 100%;
            margin: auto;
        }
        .bic-big{
            flex-basis: 42%;
        }
        .bic-sm{
          
            flex-basis: 28%;
        }
        @media (max-width:991px) {
            .bic-big{
                flex-basis: 47%;
            }
            .bic-sm{
              
                flex-basis: 47%;
            }
            .bic:last-child{
                flex-basis: 80%;

            }
        }
        @media (max-width:375px) {
            .bic-big{
                flex-basis: 100%;
            }
            .bic-sm{
              
                flex-basis: 100%;
            }
            .bic:last-child{
                flex-basis: auto;

            }
        }
        .bic{
            width: max-content;
        }
        .overlay{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 105%;
            background-color: rgba(36, 36, 36, 0.8);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            transform: scale(.5) translateY(-100px);
            opacity: 0;
            transition: .5s;
            backface-visibility: hidden;
            h3{
                font-size: 28px;
                color: rgb(255, 255, 255);
                backface-visibility: hidden;
                @media (max-width:375px) {
                    font-size: 20px;
                    
                }
                  
            }
            p{
                font-size: 14px;
                color: #fff;
                backface-visibility: hidden;

            }
        }
        .bic{
            margin-right: 7px;
            margin-bottom: 6px;
            overflow: hidden;
            &:nth-of-type(3n){
                margin-right: 0;
            }
            @media (max-width:991px) {
                margin-right: 3px;
                &:nth-of-type(3n){
                    margin-right: 3px;
                }
    
            }
            position: relative;
            &:hover{
                .overlay{
                    transform: scale(1) translateY(0);
                    opacity: 1;
    
                }
            }
        }
        
    }
    .owl-dot{
        display: inline-block;
        outline: none;
    }

    .owl-theme .owl-dots .owl-dot.active span{
        background: #9ec050;
    }
    .owl-theme .owl-dots .owl-dot span{
        background: #999;

    }
}
