                            margin-right: 12.5px;

                        }
                        @media (max-width:375px) {
                            margin-right: 0;
    
                        }
    
                        
                    }
                }
                textarea{
                    width: 100%;
                    border:1px solid  rgb(52, 52, 52);
                    height: 129px;
                    transition: .5s;
                    padding:19px 23px;
                    background-color: transparent;
                    outline: none;
                    color:rgb(86, 86, 86);
                    margin-bottom: 18px;
                    &::placeholder{
                        color: rgb(52, 52, 52);
                        font-style: italic;
                    }
                    &:focus{
