                    width: 118px;
                    height: 40px;
                    border: none;
                    border: 1px solid #9ec050;
                    cursor: pointer;    
                    font-size: 14px;
                    color: rgb(255, 255, 255);
                    font-weight: bold;
                    text-transform: uppercase;
                    transition: .5s;
                    &:hover{
                        transform: translateY(-2px);
                        background-color: transparent;
                        color: #9ec050;
                        border: 1px solid #9ec050;

                    }
                    &:active{
                        transform: translateY(0);
                        outline: none;
                    }
                }
            }
        }

    }

}
//footer
footer{
    background-color: rgb(32, 32, 32);
